.loader-container {
  z-index: 9999;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  box-sizing: border-box;
  perspective: 1000px;
  background: #979794;
  border-radius: 8px;
  width: 200px;
  height: 140px;
  transition: opacity .5s, visibility .5s;
  position: absolute;
}

.loader:before {
  content: "";
  background-color: #f5f5f5;
  background-image: linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0);
  background-position: 15px 30px, 15px 60px, 15px 90px, 105px 30px, 105px 60px, 105px 90px;
  background-repeat: no-repeat;
  background-size: 60px 10px;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border-radius: 8px;
  position: absolute;
  inset: 10px;
  box-shadow: 0 0 10px #00000040;
}

.loader:after {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  background-image: linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0), linear-gradient(#ddd 100px, #0000 0);
  background-position: 50% 30px, 50% 60px, 50% 90px;
  background-repeat: no-repeat;
  background-size: 60px 10px;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border-radius: 8px;
  width: calc(50% - 10px);
  animation: 1s linear infinite paging;
  position: absolute;
  top: 10px;
  bottom: 10px;
  right: 10px;
  transform: rotateY(0);
}

@keyframes paging {
  to {
    transform: rotateY(-180deg);
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

