.btn-loadmore {
  position: relative;
  overflow: hidden;
  background-color: transparent;
  transition: background-color 0.3s ease, color 0.3s ease;
  transition-duration: 500ms;
}

.btn-loadmore:hover,
btn-loadmore:focus {
  background-color: var(--categories-links-active-color);
  color: var(--autorization-form-btn-text-color);
}

